/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 79px;
        float: left;
    }

    .woocommerce-result-count {
        @include mkdfHeadingStyle('h5');
        line-height: #{$sselect-input-height}px ;
        padding: 1px 0;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
            text-align: initial;
            width: 300px !important;
        }
    }
	
	&.mkdf-ms-explorer {
		
		.woocommerce-ordering {
			
			.select2 {
				text-align: left;
			}
		}
	}
}



/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */