/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .mkdf-single-product-content {
		position: relative;
		display: flex;
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 46px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -15px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
						border: 1px solid $default-border-color;
					    box-sizing: border-box;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 15px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 15px;
				    width: calc(100% - 30px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 30px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 5.7% 0 0 14px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
			margin: 9px 0 11px;
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
			@include mkdfTagLineStyle('small');
		    line-height: 1;
		    margin: 22px 0 3px;

			.star-rating {
				margin-bottom: 2px;
				font-size: 16px;
			}

			a.woocommerce-review-link  {
				color: $default-text-color;
				margin-left: 26px;

				&:hover{
					color: $default-heading-color;
				}
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 25px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 36px 0 50px;
		
		    > span {
			    display: block;
				text-transform: uppercase;
				margin: 17px 0;
				@include mkdfTagLineStyle('small');

				&.mkdf-product-meta-title {
					@include mkdfHeadingStyle('h5');
					margin: 0 0 25px;
				}

				.sku {
					color: $default-text-color;
				}
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;

						&:after {
							color: $default-text-color;
						}
				    }

					&:not(:last-child):after {
						content: '';
						color: green;
						@include mkdfCategorySeparator();
					}
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
		    text-transform: uppercase;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 32px 0 0;
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 37px 0 0;
	    }
	    
	    button.single_add_to_cart_button {
		    opacity: 0;
		    
		    &.mkdf-visible {
			    opacity: 1;
			    
			    @include phone-landscape {
				    margin-top: 40px;
			    }
		    }
		
		    &.button {
			    
			    &:hover {
				    @include mkdfButtonSolidColor();
			    }
		    }
	    }
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 32px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				@include mkdfHeadingStyle('h5');
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 0 69px 0 0;
				}
				
				&:last-child {
					
					a {
						padding: 0;
					}
				}

				&.active a{
					color: $first-main-color;
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 15px 0 20px;
			}
		}
		
		table {
			margin: 13px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			tr {
				display: block;
				
				&:not(:last-child) {
					margin-bottom: 11px;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				@include mkdfTagLineStyle('small');
			}
			
			td {
				width: 99%;
				@include mkdfTagLineStyle('small');
				color: $default-text-color;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 0 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					margin-bottom: 20px;
					
					img {
						float: left;
						width: 120px;
						
						@include phone-landscape {
							float: none;
						}
					}
				}
				
				.comment-text {
					padding: 0 0 0 151px;
					margin-top: -11px;
					
					@include phone-landscape {
						padding: 0;
						margin-top: -3px;
					}
					
					p {
						margin: 0;
						
						.woocommerce-review__author {
							@include mkdfHeadingStyle('h5');
						}
					}
					
					.description {
						margin-top: 10px;
					}
					
					.star-rating span {
						font-size: 0;
						
						&:before{
							font-size: 10px;
						}
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					display: inline-block;
					margin: 0 0 20px;
					@include mkdfHeadingStyle('h5');
				}
				
				form {
					@include mkdfTagLineStyle(small);
					color: $default-heading-color;
					
					.comment-form-rating {
						margin-bottom: 25px;
					}
				}
				
				p {
					margin: 0;
					
					&.comment-form-comment label{
						display: block;
						margin-bottom: 15px;
					}
					
					#wp-comment-cookies-consent {
						position: relative;
						top: 2px;
					}
					
					.submit {
						margin-top: 48px;
					}
				}
				
				.stars {
					
					> span {
						position: relative;
						display: flex;
						margin: 10px 0;
					}
					
					&.selected {
					
						a {
							@include mkdfWooCommerceSingleProductRatingsStyle(true);
						}
					}
					
					a {
						position: relative;
						display: block;
						color: transparent;
						margin: 0;
						font-size: 0;
						line-height: 1;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2 {
			display: block;
			margin: 74px 0 70px;
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				
				@media only screen and (min-width: $ipad-landscape-plus-pixel) {
					margin-bottom: 0;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */