/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 14px;
    width: auto;
    margin: 0;
    padding: 0 10px;
    @include mkdfTagLineStyle('small');
    color: #fff;
    line-height: 21px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
}

@mixin mkdfWooCommerceSaleStyle() {
    left: 14px;
    background-color: $first-main-color;
    border: 1px solid $first-main-color;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    left: 14px;
    background-color: $default-background-color;
    border: 1px solid $default-border-color;
    color: $default-heading-color;
}

@mixin mkdfWooCommerceNewProductStyle() {
    right: 14px;
    background-color: $default-heading-color;
    border: 1px solid $default-heading-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    @include mkdfHeadingStyle(h4);
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
    
        @include ipad-landscape {
            margin: 0 4px 0 0;
        }
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 10px;
    line-height: 1;
    letter-spacing: 4px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        max-width: 99.9%;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle($active-style: false) {
    $single-rating-default-icon: "\e031";
    $single-rating-active-icon: "\e033";
    
    @if ($active-style) {
    
        &.active {
    
            &:before {
                content: $single-rating-active-icon;
            }
            
            ~ a {
    
                &:before {
                    content: $single-rating-default-icon;
                }
            }
        }
        
        &:not(.active) {
            
            &:before {
                content: $single-rating-active-icon;
            }
        }
    } @else {
        
        &:before {
            content: $single-rating-default-icon;
            @extend %star-ratings;
        }
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */