/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {
    
    strong {
        font-weight: inherit;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 30px;
        margin: 0 0 30px;
        line-height: 38px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
    
            &.button {
                margin-top: -10px!important; //important because of product single page
            }
        }
    }

    .woocommerce-info {

        .showcoupon {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */