/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .mkdf-quantity-input {
            position: relative;
            height: 54px;
            width: 54px;
            padding: 0;
            margin: 0 40px 0 0;
            @include mkdfTagLineStyle('small');
            font-size: 13px;
            outline: 0;
            color: #fff;
            background-color: $first-main-color;
            border-color: $first-main-color;
            border-top-left-radius: $default-border-radius;
            border-bottom-left-radius: $default-border-radius;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 40px;
            height: 50%;
            line-height: 28px;
            background-color: #fff;
            color: $default-heading-color;
            border-left: 0;
            cursor: pointer;
            transition: color .15s ease-in-out;
            font-family: 'ElegantIcons';
            speak: none;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            -webkit-font-smoothing: antialiased;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            top: 0;
            border-bottom: 0;
            border-top-right-radius: $default-border-radius;
        }

        .mkdf-quantity-minus {
            bottom: 0;
            border-top: 0;
            border-bottom-right-radius: $default-border-radius;
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
            padding: 13px 0;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */