/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli {
			
			.mkdf-pli-add-to-cart {
				margin: 45px 0 0;
			}
			
			.mkdf-pli-category {
				color: #fff;
			}
		}
	}

    .mkdf-pli {
        padding: 0;
        z-index: 100;

	    .mkdf-pli-title {
		    margin: 0 0 4px;
	    }
	    
	    .mkdf-pli-title,
	    .mkdf-pli-price {
		    display: inline-block;
		    width: 50%;
	    }
	
	    .mkdf-pli-category {
		    @include mkdfTagLineStyle('small');
		    display: inline-block;
		    margin: 0;
		    width: 65%;
		    
		    a {
			    color: $first-main-color;
			    
			    &:hover {
				   
			    }
		    }

			.eltdf-cat-separator {
				@include mkdfCategorySeparator();
			}
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }
	
	    .mkdf-pli-rating-holder {
		    float: right;
		    display: inline-block;
		    text-align: right;
		    margin-right: -4px;
		    @include mkdfRelativeHolderLayout();
		    width: 35%;
		
		    .mkdf-pli-rating {
			    @include mkdfWooCommerceRatingsStyle();
		    }
	    }
	
	    .mkdf-pli-title {
		    margin: 0;
		    float: left;
		    position: relative;
		    text-align: left;
		    z-index: 1;
	    }
	
	    .mkdf-pli-price {
		    @include mkdfWooCommercePriceStyle();
		    @include mkdfHeadingStyle(h5);
		    float: right;
		    text-align: right;
	    }
	
	    &:hover {
		    .mkdf-pli-text {
			    opacity: 1;
		    }
	    }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
	            box-sizing: border-box;
	            border: 1px solid $default-border-color;
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
	            @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
	            @include mkdfWooCommerceOutOfStockStyle();
            }
	
	        .mkdf-pli-new-product {
		        @include mkdfWooCommerceNewProductStyle();
	        }
        }
            
        .mkdf-pli-text {
            @include mkdfAbsoluteHolderLayout();
            padding: 20px;
            opacity: 0;
            background-color: rgba($first-main-color, 0.95);
            box-sizing: border-box;
            transition: opacity .25s ease-out;
        }

        .mkdf-pli-text-outer {
	        @include mkdfTableLayout();
	        text-align: center;
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();

            .mkdf-pli-title ,
            .mkdf-pli-category ,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating,
            .mkdf-pli-price {
                color: #fff;
	
	            &:hover{
		            color: inherit;
	            }
	            
	            a {
		            color: inherit;
		            
		            &:hover{
			            color: inherit;
		            }
	            }
            }

	        .mkdf-pli-add-to-cart {

		        &.mkdf-default-skin {

			        .button,
			        .added_to_cart {
						@include mkdfButtonPredefined();
				        background-color: transparent;
				        border-color: rgba(255, 255, 255, 0.25);
				
				        &:hover {
					        background-color: transparent;
					        border-color: rgba(255, 255, 255, 0.25);
				        }
			        }
		        }

		        &.mkdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.mkdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include mkdfButtonDefaultStyle();
	            @include mkdfButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .mkdf-pli-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
        margin: 18px 0 38px;
    }
	
	.mkdf-pli-text-wrapper-inner{
		margin-bottom: 11px;
		text-align: left;
	}
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
			}
		}
	}
	
	&.mkdf-product-info-dark {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-heading-color;
				}
				
				.mkdf-pli-category a:hover {
					color: $first-main-color;
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */