/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include mkdfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include mkdfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include mkdfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .mkdf-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .mkdf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
    
        .mkdf-widget-title {
            margin: 0 0 31px;
        }
    
        form {
            margin-top: 30px;
        }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 2px;
                background-color: #e3e3e3;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -3px;
                    height: 8px;
                    width: 8px;
                    border-radius: 0;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        transform: translateX(-50%);
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 16px 0 0;
            display: flex;
            flex-direction: column-reverse;
    
            .price_label {
                font-style: normal;
                @include mkdfHeadingStyle('h6');
                margin-bottom: 34px;
            }
            
            .button {
                align-self: flex-start;
                opacity: 0;
                
                &.mkdf-visible {
                    opacity: 1;
                }
	
	            &:hover {
		            @include mkdfButtonSolidColor();
	            }
            }
        }
    }
    
    &.widget_product_categories {
        
        .mkdf-widget-title {
            margin: 0 0 23px;
        }
        
        ul {
            
            li {
                margin: 7px 0;
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .mkdf-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 110px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                @include mkdfTagLineStyle('small');
                color: $default-heading-color;
                font-size: 11px !important;
    
                &:not(:last-child):after {
                    content: '';
                    @include mkdfCategorySeparator();
                    color: $default-heading-color;
                }
                
                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include mkdfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include mkdfRelativeHolderLayout();
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 56px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                transition: color .2s ease-out;
            }
            
            input {
                width: calc(100% - 50px);
                padding: 10px 10px 10px 30px;
                font-family: inherit;
	            @include mkdfHeadingStyle('h6');
	            font-size: 14px;
	            border: 1px solid $default-border-color;
	            border-radius: $default-border-radius;
	            box-sizing: border-box;
	            transition: border-color .2s ease-out;
	
	            &:focus {
		            border-color: $first-main-color;
	            }
    
                @include mkdfPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 50px;
                padding: 0;
                font-size: 15px;
	            text-align: center;
	            cursor: pointer;
	            color: #fff;
	
	            &:after {
		            content: '';
		            position: absolute;
		            display: block;
		            right: 0;
		            top: 0;
		            height: calc(100% - 2px);
		            width: 50px;
		            background-color: $first-main-color;
		            border: 1px solid $first-main-color;
		            border-top-right-radius: $default-border-radius;
		            border-bottom-right-radius: $default-border-radius;
		            z-index: 1;
	            }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                    
                    &.icon_search {
	                    position: relative;
                        transform: scaleX(-1);
	                    z-index: 2;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */