/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }
    
    &:hover {
        
        .mkdf-sc-dropdown {
            max-height: 100vh;
            pointer-events: auto;
	        transition: .35s ease-in .1s;
        }
    }

    .mkdf-shopping-cart-inner {
        @include mkdfRelativeHolderLayout();
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;

        .mkdf-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-sc-opener-icon {
                font-size: 18px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
	    
	    &.mkdf-header-cart-predefined {
		    top: -1px;
            color: $default-heading-color;
		    
		    .mkdf-sc-opener-icon {
			    @include mkdfHeadingStyle('h6');
			    margin-right: 9px;
			    color: currentColor;
		    }
		
		    .mkdf-sc-opener-count {
			    position: relative;
			    top: -1px;
			    width: 16px;
			    height: 16px;
			    font-family: $default-tagline-font;
			    font-size: 9px;
			    line-height: 18px;
			    font-weight: 400;
			    text-align: center;
			    border-radius: 2px;
			    box-sizing: border-box;
			    color: $default-heading-color;
			    background-color: #e5e5e5;
			    transition: all .3s ease-in-out;
		    }
		    
		    &:hover {
			
			    .mkdf-sc-opener-count {
				    color: #fff;
				    background-color: $first-main-color;
			    }
		    }
	    }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: -22px;
    text-align: left;
    box-sizing: border-box;
    max-height: 0;
    pointer-events: none;
    overflow: hidden;
    will-change: transform;
    transition: .2s;
    
    .mkdf-sc-dropdown-inner {
        @include mkdfRelativeHolderLayout();
        width: 391px;
        background-color: #fff;
        border-radius: $default-border-radius;
        box-shadow: $menu-box-shadow;
        box-sizing: border-box;
	    padding: 24px 26px 22px 30px;
    }
    
    .mkdf-empty-cart {
        @include mkdfHeadingStyle('h6');
    }
    
    .mkdf-sc-dropdown-items {
        @include mkdfRelativeHolderLayout();
	    margin-top: 36px;
    }
    
    .mkdf-sc-dropdown-item {
        position: relative;
        display: flex;
        align-items: flex-start;
        margin-top: 19px;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .mkdf-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 70px;
        margin-right: 14px;
        
        a, img {
            display: block;
        }
    
        img {
            border: 1px solid $default-border-color;
            box-sizing: border-box;
        }
    }
    
    .mkdf-sc-dropdown-item-content {
        @include mkdfRelativeHolderLayout();
        padding-right: 20px;
    }
    
    .mkdf-sc-dropdown-item-title {
        margin: 5px 0 0;
    }
    
    .mkdf-sc-dropdown-item-quantity {
        margin: 8px 0 0;
        @include mkdfTagLineStyle('small');
        color: $first-main-color;
    }
    
    .mkdf-sc-dropdown-item-price {
        margin: 4px 0 0;
        @include mkdfHeadingStyle('h6');
    }
    
    .mkdf-sc-dropdown-item-remove {
        position: absolute;
        top: -1px;
        right: 0;
        font-size: 26px;
        line-height: 1;
    }
    
    .mkdf-sc-dropdown-subtotal {
        position: relative;
        display: flex;
	    width: 100%;
	    margin: 39px -30px 0;
	    padding: 16px 30px;
	    background-color: #f5f5f5;
        align-items: center;
        justify-content: space-between;
        
        > * {
            margin: 0;
	        @include mkdfHeadingStyle('h5');
        }
    }
    
    .mkdf-sc-dropdown-button-holder {
        @include mkdfRelativeHolderLayout();
	    margin: 40px 0 38px;
    }
    
    .mkdf-sc-dropdown-button {
	    position: relative;
	    display: block;
	    width: 100%;
	    margin: 0;
	    padding: 12px 25%;
	    font-family: $default-heading-font;
	    font-size: 14px;
	    letter-spacing: 0.1em;
	    line-height: 2em;
	    font-weight: 700;
	    text-transform: uppercase;
	    outline: none;
	    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
        @include mkdfButtonPredefined();
    }
}

.mkdf-vertical-menu-area {

    .mkdf-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */